.contents {
  .main__title {
    display: none;
  }

  .d-menu {
    display: none;
  }

  @media screen and (min-width: 769px){
    max-width: 1440px;
    display: flex;
    margin: 0 auto;
    padding: 0 20px;

    .main__title {
      display: block;
      font-size: 3.5vmin;
      margin-bottom: 20px;
    }

    .d-menu {
      display: flex;
      flex-direction: column;

      .d-menu__title {
        font-size: 3.5vmin;
        font-family: circe;
        font-weight: 500;
        margin-bottom: 30px;
      }
      .d-menu__list {
        margin: 0;
        padding: 0 20px;
        list-style: none;
        display: flex;
        flex-direction: column;
      }
      .d-menu__elem {
        padding: 20px 0;
        border-bottom: 2px solid #e7ebef; 
        position: relative;
        &:hover {
          &:before {
            position: absolute;
            width: 90%;
            content: '';
            display: block;
            height: 5px;
            border-radius: 5px;
            background-color: #6ad3ff;
            bottom: 10px;
            left: 0;
          }
        }
      }
      .d-menu__link {
        font-size: 3vmin;
        font-weight: 400;
        text-decoration: none;
        color: #000;
        position: relative;
        &:active,
        &:hover,
        &.active {
          font-weight: 500;
        }
      }

    }

    .contents__elem {
      width: 30%; 
    }
    .contents__elem:last-child {
      width: 70%; 
      margin-left: 20px;
    }

    .discount {
      .title {
        height: 285px;
        div {
          font-size: 3.5vmin;
        }
      }
      .info {
        padding: 50px;
        font-size: 3vmin;
      }
    }
  }
}