.content.padd.cabinet {
  .resulted {
    display: none;
  }
  @media screen and (min-width:769px){
    max-width: 1440px;
    margin: 0 auto;

    .title.main__title {
      text-align: center;
      font-size: 4.5vmin;
    }

    .c_title {
      display: none;
    }

    .cabinet__list {
      display: flex;
      justify-content: space-between;
    }
    .cabinet__elem {
      width: 40%;
      padding: 20px 0;
      &:last-child {
        width: 60%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0 30px;
      }

      .history {
        .item {
          display: flex;
          justify-content: space-between;
          padding: 0 20px;
          .status {
            flex: 1 1 auto;
            min-width: 15px;
            min-height: 15px;
            display: flex;
            align-items: center;
            img {
              width: 100%;
              height: auto;
              margin: 0;
              padding: 0;
            }
          }
          .ico {
            flex: 1 1 auto;
            padding: 10px;
            min-width: 54px;
            min-height: 54px;
            img {
              width: 100%;
              height: auto;
              margin: 0;
              padding: 0;
            }
          }
          .time {
            flex: 1 1 auto;
            display: flex;
            align-items: center;
            font-size: 2vmin;

          }
          .price {
            flex: 2 1 auto;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            font-size: 2.5vmin;
          }
        }
        .more {
          padding: 10px 40px;
          font-size: 2.5vmin;
        }
      }
      .balance {
        margin: 0;
        margin-bottom: 30px;
        font-size: 4.5vmin;
      }
      .withdraw.des {
        margin: 0;
        margin-bottom: 30px;
        padding: 20px 30px;
        font-size: 2.5vmin;
        max-width: 450px;
      }
      .waiting {
        .item {
          .price {
            padding: 10px;
            font-size: 4vmin;
          }
          .date {
            padding: 10px;
            font-size: 2.5vmin;
          }
          .status {
            padding: 10px;
            font-size: 2.5vmin;
          }
        }
      }
    }
    
    .prints_pays {
      margin-top: 40px;
      .tabs {
        margin-bottom: 30px;
        display: flex;
        justify-content: center;
        a {
          font-size: 3.5vmin;
          padding: 30px;
          width: 30%;
        }
      }
      .windows .window.is {
        display: flex;
      }
      .window {
        flex-direction: column;
        padding-left: 40px;

        .add_print {
          order: -1;
          padding: 40px 40px 40px 120px;
          max-width: 550px;
          margin:0;
          margin-bottom: 20px;
          font-size: 3.5vmin;
          &:before {
            width: 65px;
            height: 61px;
            top: 20px;
            left: 20px;
            margin: 0;
          }
        }
        .text {
          div {
            font-size: 2.5vmin;
          }
          order: -1;
          font-size: 2vmin;
          margin-bottom: 30px;
        }
        .on_check {
          padding-top: 0;
          position: relative;
          &:before {
            position: absolute;
            content: 'Не в продаже';
            font-family: "circe";
            font-weight: 500;
            font-size: 3vmin;
            min-width: 150px;
            height: 25px;
            transform: rotate(-90deg);
            top: 10vmin;
            left: -12vmin;
          }
          .sub_title, .title {
            display: none;
          }
        }
        .ch_items {
          margin-top: 0;
          
          .item {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding-right: 40px;
            position: relative;
            .ico {
              width: 128px;
              height: 128px;
              padding: 0;
            }
            .name {
              margin-left: 20px;
              font-size: 3.5vmin;
              div {
                display: none;
                font-size: 3vmin;
              }
            }
            .delete {
              position: absolute;
              width: 50px;
              height: 50px;
              margin: 0;
              padding: 0;
              bottom: 30px;
              right: 20px;
            }
            .resulted {
              display: flex;
              justify-content: center;
              flex-direction: column;
              align-items: center;
              flex:2 1 auto;
              font-family: "circe";
              .resulted__title {
                font-size: 3.5vmin;
                font-weight: 500;
                position: relative;
                &:before {
                  content: '';
                  position: absolute;
                  left: -60px;
                  top: 40%;
                  width: 30px;
                  height: 30px;
                  background: url(../img/m_img_6.png) no-repeat center center;
                  background-size: 100%;
                  transform: translateY(-20px);
                }
              }
              .resulted__sub_title {
                font-size: 3vmin;
                font-weight: 400;
                color: #7a8494;
              }
            }
            .resulted_bad {
              .resulted__title {
                &:before {
                  left: -60px;
                  top: 40%;
                  width: 40px;
                  height: 40px;
                  transform: translateY(-25px);
                  background: url(../img/m_img_7.png) no-repeat center center;
                  background-size: 90%;
                }
              }
            }
          }
        }
        .rejected {
          padding-top: 0;
          .sub_title, .title {
            display: none;
          }
        }
        .on_sale {
          position: relative;
          padding-top: 0;
          margin-top: 40px;
          &:before {
            position: absolute;
            content: 'В продаже';
            font-family: "circe";
            font-weight: 500;
            font-size: 3vmin;
            min-width: 150px;
            height: 25px;
            transform: rotate(-90deg);
            top: 10vmin;
            left: -15vmin;
            background: url(../img/m_img_6.png) no-repeat left center;
            background-size: 20%;
            padding-left: 50px;
          }
          .title {
           display: none;
          }
          table {
            margin-top: 0;
          }
          th {
            font-size: 3vmin;
          }
          td {
            font-size: 3.5vmin;

            img {
              max-width: 93px;
              max-height: 93px;
            }
          }
        }
        .pays {
          .title {
            font-size: 4  vmin;
          }
          th {
            font-size: 3vmin;
          }
          td {
            font-size: 3.5vmin;
          }
        }
      }
    }
  }
}