.content.orders {
  .title.main__title {
    display: none;
  }
  @media screen and (min-width: 769px) {
    max-width: 1440px;
    margin: 0 auto;
    .title.main__title {
      display: block;
      font-size: 4vmin;
      padding: 0 20px;
      margin-bottom: 30px;
    }

    .my_orders {
      .s_title {
        font-size: 3vmin;
        text-align: left;
      }
      .order {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        .order__description {
          width: 48%;
          display: flex;
          flex-wrap: wrap;
        }

        .order__elem {
          width: 48%;
          display: flex;
          flex-direction: column;
          justify-content: center;
        }

        .status {
          width: 100%;
          order: 3;
        }
        .description {
          max-width: 287px;
          display: flex;
          flex-direction: column;
          align-items: center;  
          padding: 10px;
          .ico {
            width: 213px;
            display: flex;
            justify-content: center;
            img {
              width: 100%;
             height: auto;
             max-width: 287px;
             max-height: 230px;
            }
          }
          .main {
            padding: 0;
            display: flex;
            align-items: center;
            flex-direction: column;
            text-align: center;
            font-size: 3vmin;
            div {
              font-size: 2.5vmin;
            }
          }
        }
        .info {
          padding: 20px;
          width: 100%;
          display: flex;
          flex-direction: column;

          .i_titles,
          .c_view {
            display: flex;
            justify-content: space-between;
            width: 100%;
            font-size: 2vmin;

            .number.t_c {
              width: 40%;
              display: flex;
              align-items: center;
              justify-content: center;

            }
            .price.t_r {
              width: 20%;
              display: flex;
              align-items: center;
              justify-content: center;
            }
            .delivery {
              width: 40%;
              display: flex;
              align-items: center;
              justify-content: center;
              .ico {
                width: 30px;
                height: 30px;
              }
              .d .title {
                font-size: 2vmin;
              }
            }
          }
        }
        .result {
          padding: 20px;
        }
        .i_title {
          font-size: 2vmin;
          padding-bottom: 10px;
        }
        .number {
          font-size: 2.5vmin;
        }
        .full {
          .price-val {
            font-size: 3.4vmin;
          }
          .rub {
            font-size: 2.8vmin;
            padding-left: 5px;
          }
        }
      } 
      .result {
        display: flex;
        flex-direction: column;
        align-items: center;

        .inf {
          padding: 0;
          font-size: 3vmin;
          margin-bottom: 20px;
        }
        .track {
          margin: 0;
          padding: 15px 30px;
          font-size: 2.5vmin;
        }
      }
    }      
  }
}
