.login.contents {

  @media screen and (min-width: 769px){
    max-width: 1440px;
    margin: 0 auto;
    justify-content: space-between;
    .contents__elem {
      padding: 0 20px;
      width: 45%;
      &:last-child {
        width: 45%;
      }

      .info {
        padding-top: 10px;
        padding-bottom: 10px;
        margin: 0;

        .name {
          font-size: 3.5vmin;
        }
        .email {
          font-size: 3vmin;
        }
      }

      .login {
        .s_title {
          padding: 20px 0;
          font-size: 3vmin;
        }
        .buttons {
          display: flex;
          justify-content: space-between;
          min-height: 80px;
          margin-bottom: 20px;

          .login_soc {
             padding: 10px 20px; 
             font-size: 2.5vmin;
             width: 50%;
             margin: 0 10px;
             position: relative;
          }
          .login_soc.vk {
            &:after {
              position: absolute;
              width: 80px;
              height: 80px;
              top: 50%;
              left: 0;
              right: 0;
              bottom: 0;
              margin: 0 auto;
              transform: translateY(-40px);
            }
          }
          .default {
            padding: 10px 20px; 
            font-size: 2.5vmin;
            width: 50%;
            margin: 0;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 10px;
          }
        }
        .login_soc {
          padding: 20px 10px 20px 60px; 
          font-size: 2.5vmin;
          width: 100%;
          margin: 20px 0;
          position: relative;
          text-align: center;
       }
       .login_soc.fb {
        &:after {
          position: absolute;
          width: 80px;
          height: 80px;
          top: 50%;
          left: 5px;
          right: 0;
          bottom: 0;
          transform: translateY(-40px);
        }
      }
      .login_soc.go {
        &:after {
          position: absolute;
          width: 80px;
          height: 80px;
          top: 50%;
          left: 5px;
          right: 0;
          bottom: 0;
          transform: translateY(-40px);
        }
      }
     }
      .s_title {
      font-size: 3.5vmin;
      padding: 20px;
      }
      .login, .ch_pass {
        input {
          padding: 10px;
          font-size: 2.5vmin;
          border-bottom: 3px solid #000031;
          margin: 10px 0;
        }
        .submit {
          margin-top: 30px;
          .des, .default {
            border-bottom:0;
          }
        }
      }

    }
  }
}