.footer {
  background-color: #f0f3f5;
  margin-top: 30px;
  .navigation {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #fff
  }

  .dispatch {
    width: 100%;
  }

  .payment_methods {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .links {
    width: 78%;
    .p2 {
      width: auto;
      float: left;
    }

    a {
      &:hover {
        color: rgba(0,0,49,.43);
      }
    }
  }
  .social {
    .m_title {
      display: none;
    }
  }
  @media screen and (min-width: 769px) {
    
    .navigation {
      max-width: 1440px;
      margin: 0 auto;
    }
    .links {
      display: flex;
      width: 60%;
      padding: 10px;

      .p2 {
        width: 25%;
      }
      .p2:last-child {
        width: 70%;
        display: flex;
      }
      .m_title {
        font-size: 3.5vmin;
        padding: 5px 10px;
      }
      ul li a {
        font-size: 3vmin;
      }
    }  
    .dispatch {
      border: 0;
      width: 40%;
      padding: 0 10px;
      background: #fff;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      justify-content: flex-start;

      .title {
        font-size: 3.5vmin;
      }
      .text {
        font-size: 3vmin;
      }
      
      form {
        margin-top: 20px;
        width: 100%;
        input.dispatch__text {
          padding: 5px 20px;
          font-size: 3vmin;
        }
  
        input.dispatch__submit {
          top: 0;
          right: 0;
          width: 20%;
        }
      }
    }
    .payment_methods {
      width: 63%;
      padding-top: 30px;
      background-color: #f0f3f5;
      .m_title {
        font-size: 3.5vmin;
        padding: 10px 20px;
      }
      .methods_line {
        margin-top: 0;
        .methods {
          display: flex;  
          justify-content: space-between;
          width: 50%;
          height: 40px;
          .method {
            height: 40px;
            flex: 1 1 auto;
            max-width: 80px;
            margin-left: 10px;
            
            &::first-child {
              margin-left: 0px;
            }
          }
        }
      }
    }
    .social {
      order: 2;
      background-color: #f0f3f5;
      display: flex;
      flex-direction: column;
      width: 37%;
      border: 0;
      padding: 0;
      padding-top: 30px;
      .m_title {
        display: block;
        font-size: 3.5vmin;
        padding: 10px 20px;
      }
      .social__list {
        display: flex;
        justify-content: flex-start; 
        a {
          flex: 1 1 30%;
          max-width: 80px;
          margin-left: 20px;
          height: 40px;

          &::first-child {
            margin-left: 0;
          }
        }
        .vk, .fb, .inst {
          background-size: contain;
        }
      }
    }
  }
  @media screen and (min-width: 1441px) {
    background: #fff;
    .social, .payment_methods {
      background: #fff;
    }
  }
}