.content.artist {

  @media screen and (min-width:769px){
    max-width: 1440px;
    margin: 0 auto;

    .user {
      text-align: left;
      .name {
        font-size: 3.5vmin;
      }
    }
    .s_title {
      font-size: 3.5vmin;
      text-align: left;
    }
    .items {
      margin: 20px 0 40px 0;
    }
    .tags {
      display: flex;
      justify-content: flex-start;
      margin: 0;
      padding: 0;
      list-style: none;
      max-width: 1440px;
      margin-top: 20px;

      li {
        margin-left: 2vmin;
        
          &:first-child {
            margin-left: 0;
          }
      }

      a {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 15vmin;
        height: 6vmin;
        max-width: 138px;
        max-height: 55px;
        color: #000031;
        font-family: circe;
        font-weight: 400;
        font-size: 2.5vmin;
        text-decoration: none;
        box-shadow: 0 4px 10px rgba(52, 70, 74, 0.29);
        background-color: #fff;
        border-radius: 25px;
      }
    }
  }
}