.popup.v .content {
  .window {
    @media screen and (min-width: 769px){
      max-width: 640px;
      max-height: 450px;
      padding: 20px 40px;
      margin: 0 auto;
  
      .title {
        margin: 0;
        font-size: 3.5vmin;
        margin-bottom: 20px;
      }
      .text  {
        font-size: 3vmin;
        margin-bottom: 20px;
        margin: 0;
        margin-bottom: 20px;
        line-height: 1.4;
      }
      button {
        display: block;
        margin: 0;
        width: 50%;
        margin: 0 auto;
        padding: 10px 20px;
        font-size: 3vmin;
  
      }
      .close {
        top: 5px;
        right: 5px;
        width: 30px;
        height: 30px;
        div {
          top: 50%;
          left: 0;
          width: 30px;
          height: 5px;
          margin-left: -15px;
          margin-top: 0;
        }
      }
      
    }
    
  }
}