.content.login {
  .main-title, .login__exit {
    display: none;
  }
  @media screen and (min-width: 769px){
    /* Style for "Прямоуголь" */
    width: 758px;
    height: 514px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0 4px 10px rgba(52, 70, 74, 0.29);
    background-color: #ffffff;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;

    .main-title {
      display: block;
      color: #132136;
      font-family: "circe";
      font-size: 4vmin;
      font-weight: 500;
      text-align: center;
    }
    .login__exit {
      display: block;
      width: 40px;
      height: 40px;
      cursor: pointer;

      &::after{
        display: block;
        content: "\00d7";
        font-size: 50px;
        position: absolute;
        top: 20px;
        right: 20px;
      }
    }

    form {
      display: block;
      width: 80%;
      margin: 0 auto;
      input {
        font-size: 2.5vmin !important;
        padding: 20px;
        border-bottom: 3px solid #000031 !important;
      }

      .submit {
        margin-top: 40px;
        .default {
          border-bottom: 0 !important;
        }
      }
      .default {
        border: 0 !important;
      }
    }
    .g_text.t_c {
      font-size: 3vmin;
      margin-top: 40px;
    }
    .login_soc {
      padding: 20px 10px 20px 60px; 
      font-size: 2.5vmin;
      width: 80%;
      position: relative;
      text-align: center;
      margin: 10px auto;
   }
    .login_soc.fb, .login_soc.vk, .login_soc.go {
      &:after {
        position: absolute;
        width: 80px;
        height: 80px;
        top: 50%;
        left: 5px;
        right: 0;
        bottom: 0;
        transform: translateY(-40px);
      }
    }
    .s_text {
      text-align: center;

    }
    .buttons {
      display: flex;
      justify-content: space-between;
      min-height: 80px;
      margin-bottom: 20px;

      .default {
        padding: 10px 20px; 
        font-size: 2.5vmin;
        width: 50%;
        margin: 0;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 10px;
      }
    }
  }
}