.content.good {
  .contents {
    .title.main__title {
      display: none;
    }
    .slider__list {
      display: none;
    }
    @media screen and (min-width: 769px){

      max-width: 1440px;
      margin: 0 auto;

      .title.main__title {
        display: block;
      }

      .slick-slider {
        display: none;
        width: 100%;
      }

      .slider__list {
        display: flex;
        flex-wrap: wrap;

        .slide {

          width: 46%;
          margin: 10px;
          min-width: 340px;

          @media screen and (max-width: 1080px) {
            width: 100%;
          }
          .img {
            height: 330px;  
          }
          .title {
            font-size: 3vmin;
          }
          .text {
            font-size: 2.5vmin;
            line-height: 1.3;
          }
        }
      }

      .reviews {
        font-size: 3.5vmin;
      }
    }
  }
}