.artist.contents {

  @media screen and (min-width: 769px) {
    max-width: 1440px;
    margin: 0 auto;
    //flex-wrap: wrap;
    .contents__elem {
      width: 60%;
      position: relative;

      &:last-child {
        width: 38%;
        display: flex;
        align-items: center;
      } 

      .from_to {
        margin: 20px 0;
        img {
          width: 80px;
          height: 80px;
        }
        &::before {
          height: 45px;
          width: 80px;
        }
      }
      .big_title {
        font-size: 6vmin;
        margin-bottom: 50px;
      }
      .des {
        padding: 20px 30px;
        font-size: 3.5vmin;
        margin: 20px 0 60px 0;
        display: none;
      }
      .wishes {
        display: flex;
        flex-direction: column;
      }
      .wish {
        padding: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        max-width: 40 0px;
        max-width: 360px;

        &::before {
          width: 150px;
          height: 80px; 
          margin-top: -50px;
          left: 50%;
          transform: translateX(-20px);
        }
        .ico {
          //max-width: 130px;
          margin-right: 0;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          &::before {
            right: -20px;
          }
          div {
            font-size: 5vmin;
            margin: 0 !important;
            width: 40%;
          }
          font-size: 3vmin;
          img {
            max-width: 80px;
            max-height: 80px;
          } 
        }
        .cont {
          font-size: 2.5vmin;
          padding: 10px;
          line-height: 1.2;
          margin-left: 20px;
          div {
            font-size: 3vmin;
            line-height: 1;
          }
        }
      }
      .wish.money {
        margin: 0 auto;
        margin-bottom: 30px;
        max-width: 100%;
        width: 98%;
      }
      .wish__elem {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        .wish {
          margin: 10px 10px;
        }
      }
      .content.login {
        padding: 10px;
        background: #fff;
        border-radius: 10px;
        width: auto;
        height: auto;
        position: relative;
        margin: 0 auto;

        .login_soc {
          margin-bottom: 20px;
          padding: 25px 25px 25px 90px;
          font-size: 2vmin;
          &::after {
            // width: 80px;
            // height: 55px;
            // top: 1vmin;
            // left: 0;
          }
        }
        .s_text {
          margin: 15px;
          font-size: 2vmin;
        }
        .buttons {
          a {
            padding: 20px 50px;
            font-size: 2vmin;
          }
        }
      }
    }
  }
}
