.top-menu {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.top-menu__elem {
  margin-left: 38px;
  display: flex;
  align-items: center;

  &:first-child {
    margin-left: 0;
  }
}

.top-menu__link {
  color: #000031;
  font-family: circe;
  font-weight: 400;
  font-size: 16px;
  text-decoration: none;
  padding: 0;

  &:active,
  &:hover {
    color: rgba(0, 0, 49, 0.43);
  }
}
