.submenu {
  display: flex;
  justify-content: flex-start;
  margin: 0;
  padding: 0;
  list-style: none;
  max-width: 680px;
  margin-top: 20px;
}

.submenu__elem {
  margin-left: 2vmin;

  &:first-child {
    margin-left: 0;
  }
}

.submenu__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15vmin;
  height: 6vmin;
  max-width: 138px;
  max-height: 55px;
  color: #000031;
  font-family: circe;
  font-weight: 400;
  font-size: 2.5vmin;
  text-decoration: none;
  box-shadow: 0 4px 10px rgba(52, 70, 74, 0.29);
  background-color: #fff;
  border-radius: 25px;

  &:active,
  &:hover {
    background-color: #b8bac3;
    color: #fff;
  }

 @media screen and (min-width: 1440px){
  font-size: 2vmin;
 }
}
