.header {
  @media screen and (min-width: 769px) {
    position: relative;
    box-shadow: none;
    margin-bottom: 30px;
    top: 0 !important;
  }
}
.header .logo {
  @media screen and (min-width: 769px) {
    width: auto;
    height: auto;
    text-align: center;
  }
}

.header.na:not(.nad) {
  top: -21vmin;

  @media screen and (min-width: 769px) {
    top: 0;
  }
}

.header__top {
  margin-bottom: 17px;
  background-color: #fbfcfc;

  @include tablet() {
    display: none;
  }

  .container {
    min-height: 38px;
    align-items: center;
    justify-content: space-between;
    flex-wrap: nowrap;

    @include tablet() {
      flex-wrap: wrap;
      padding: 5px 0;
    }
  }

  .header__top-menu {
    flex-grow: 0;
  }

  .header__top-adv {
    flex-grow: 1;
  }

  .adv {
    font-family: circe;
    font-weight: 400;
    color: #9ea0ab;
    font-size: 16px;
    text-align: center;
  }
}

.header__content {
  margin-bottom: 30px;

  .container {
    justify-content: space-between;
    min-height: 92px;
    align-items: center;
  }

  .header__content-elem {
    margin-left: 20px;
    background: none;
    flex: 1 1 30%;

    &:first-child {
      margin-left: 0;
    }
  }

  @include tablet {
    display: none;
  }
}

.header__select {
  background-color: #f0f3f5;

  .container {
    justify-content: center;
  }
  @include tablet() {
    display: none;
  }
}

.header__menu {
  .container {
    min-height: 127px;
    justify-content: center;
  }
  @include tablet() {
    display: none;
  }
}

.tabnavigation {
  nav {
    overflow-x: auto;
  }
  @media screen and (min-width: 769px) {
      overflow-x: visible;
      overflow-y: visible;
      nav {
        display: none;
      }
  }
}
.history nav {
  overflow-x: auto;
}