.content {
  .items_package {
    margin: 20px 0px;
    max-width: 1440px;
    margin: 0 auto;

    .package_item {
      .more_big {
        display: none;
      }
    }
  }
  .submenu {
    display: none;
  }
  .items {
    max-width: 1440px;
    margin: 20px auto;
    padding: 0 20px;
    display: flex;
    overflow: hidden;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    
    @media screen and (max-width:768px) {
      padding: 0;
    }


    .item {
      max-width: 328px;
      padding: 10px;
      box-shadow: 0 4px 10px rgba(52, 70, 74, 0.29);
      background-color: #fff;
      cursor: pointer;
      margin: 10px 10px;

      @media screen and (max-width:768px) {
        max-width: 364px;
        max-width: 47%;
        margin: 4px;
      }

      .info {
        font-size: 3vmin; 
        padding: 5px 30px 50px 5px;

        @include tablet {
          padding: 0;
        }
      }
      .price {
        bottom: 10px;
        right: 10px;
      }
    }
  }
  .items_pack {
    .items_line {
      .item {
        .title {
          width: 100%;
          border: 0;
          font-size: 2.5vmin;
          padding: 0 10px;
          margin-top: 5px;
        }
        .info {
          font-size: 2vmin;
          font-weight: 500;
          text-align: left;
          padding: 0 23px 14px 17px;
          color: #9da3ac;
          div {
            color: #0f1c29;
          }
        }
        .price {
          bottom: 10px;
          right: 10px;
          font-size: 2vmin;
          font-weight: 500;
          color: #0f1c29;
          text-align: right;
          padding-right: 20px;
        }
      }
    }
  }
  .s_more_all {
      display: inline-block;
    position: relative;
      width: 2vmin;
      height: 2vmin;
      background: url("../img/m_star.png");
      background-size: cover;
    margin-right: 5px;
  }

  @media screen and (min-width: 769px) {
    .s_more {
      display: block;
      max-width: 425px;
      margin: 0 auto;
      padding: 30px;
      font-size: 3vmin;
      position: relative;
    }


    .submenu {
      display: flex;
    }
    .items_package {
      padding: 0 20px;
      .title {
        font-size: 3.5vmin;
      }
      .package_item {
        background: #f0f3f5;
        border: 0;
        padding: 0 20px;
        box-shadow: none;       
        .more {
          font-size: 3.5vmin;
        }
        .more_small {
          display: none;
        }
        .more_big {
          display: inline-block;
        }
        .title.sub {
          &:before, div {
            display: none;
          }
        }
      }
      
    }
    .items_pack {
      padding: 0 20px;
      .items_line {
        display: flex;

        .item {
          background-color: #fff;
          width: 213px;
          max-height: 305px;
          margin-left: 20px;
          padding: 20px 0px;
          box-shadow: 0 4px 10px rgba(52,70,74,.29);
          display: flex;
          flex-direction: column;
          border-radius: 0.7475vmin;

          img {
            width: 200px;
            height: 200px;
          }

          .title {
            width: 100%;
            border: 0;
            font-size: 2.5vmin;
            padding: 0 10px;
            margin-top: 5px;
          }
          .info {
            font-size: 2vmin;
            font-weight: 500;
            text-align: left;
            padding: 0 23px 14px 17px;
            color: #9da3ac;
            div {
              color: #0f1c29;
            }
          }
          .price {
            bottom: 10px;
            right: 10px;
            font-size: 2vmin;
            font-weight: 500;
            color: #0f1c29;
            text-align: right;
            padding-right: 20px;
          }
        }
      }
    }
  }
  @media screen and (min-width: 1400px){
    .items { 
      justify-content: flex-start;
    }
  }
}

.content_category {
  @media screen and (min-width: 769px) {
    .history {
      width: 100%;
      display: block;
    }
  }
}
