.actions {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  max-width: 340px;
}

.actions__elem {
  margin-left: 20px;

  &:first-child {
    margin-left: 0;
  }
}

.actions__link {
  display: flex;
  text-decoration: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #000031;
  font-family: circe;
  font-size: 16px;
  font-weight: 500;

  &:active,
  &:hover {
    text-decoration: none;
  }

  @media screen and (min-width: 1200px) {
    font-size: 18px;
  }
}

.action__img {
  margin-bottom: 15px;
}

.action__caption {
  text-align: center;
}

.action__img_person {
  @include sprite($s-user);
}

.action__img_pack {
  @include sprite($s-package);

  height: 34px;
}

.action__img_bag {
  @include sprite($s-bag);

  position: relative;
}

.action__count {
  position: absolute;
  box-shadow: -0.7475vmin -0.7475vmin 0 -0.24916666666667vmin #6ad3ff;
  border: 0.24916666666667vmin solid #fff;
  background: #ff3636;
  background: linear-gradient(#ff0b99, #ff3636);
  color: #fff;
  font-size: 1.6vmin;
  width: 2.5vmin;
  height: 2.5vmin;
  text-align: center;
  line-height: 16px;
  border-radius: 50%;
  font-weight: 600;
  left: 20px;
  top: -15px;

  @media screen and (min-width: 1440px) {
    left: 30px;
    top: -15px;
  }
}

.action__count-value {
  font-family: circe;
  font-size: 13px;
  font-weight: 400;
}

.actionbar {
  display: none;

  @include tablet() {
    display: block;
  }
}

.history {
  display: none;
  
    @include tablet() {
      display: block;
    }
}
