.content.del_page {
  @media screen and (min-width: 769px) {
    padding: 0;
  }
  .del {
    .title {
      text-align: center;
    }
    .price.w,
    .price {
      width: 100%;
      display: flex;
      justify-content: center;
      margin-top: 0;
      
      .price-val,
      .rub {
        font-size: 4vmin;
      }
    }
    .map_link {
      position: relative;
      margin: 0 auto;
      padding: 20px 10px;
      max-width: 300px;
      right: 0;
      bottom: 0;
      margin-top: 20px;

    }
  }
  .delivery,
  .delivery_about {
    .s_map {
      display: none;
    }
    @media screen and (min-width: 769px) {
      .contents__elem {
        width: 30%;
      }

      .contents__elem:last-child {
        width: 70%;
        margin-left: 20px;
        padding-top: 0;
      }

      .search {
        width: 100%;
        max-width: 100%;
      }
      input.delivery__input {
        width: 100%;
        padding: 10px 20px;
        font-size: 3vmin;
      }
      input.delivery__submit {
        width: 25px;
        height: 25px;
        padding: 0;
        margin: 0;
        top: 0px;
        right: 10px;
        border: 0;
        background-size: contain;
        transform: translateY(12px);
      }

      .s_title {
        font-size: 3.5vmin;
      }
      .s_list {
        display: flex;
        flex-wrap: wrap;
        @media screen and (max-width:978px){
          justify-content: center;
        }
      }
      .del {
        padding: 20px 5px 20px 70px;
        width: 300px;
        max-height: 309px;
        overflow: hidden;
        margin: 5px;

        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .title, .price-val, .rub {
          font-size: 3vmin;
        }
        .title {
          text-align: left;
        }
        .text {
          font-size: 1.7vmin;
          margin: 0;
          padding: 0;
          line-height: 1.2;
          text-align: left;
        }
        .ico {
          top: 10px;
          left: 10px;
          width: 50px;
          height: 50px;
        }
        .price.w,
        .price {
          margin-top: 20px;
          .price-val,
          .rub {
            font-size: 2.5vmin;
          }
        }
      }
      .del.action{
        &:after {
          font-size: 2.5vmin;
          right: 10px;
          bottom: 5px;
          top: auto;
          text-align: center;
          writing-mode: initial;
          transform: scale(1);
        }
      }
      .map_link {
        display: none;
      }
      .s_map {
        display: block;
        width: 610px;
        height: 245px;
        margin: 5px;
        box-shadow: 0 0.62291666666667vmin 1.495vmin -0.37375vmin rgba(52,70,74,0.29);

        .s_map__img {
          max-width: 100%;
          height: 100%;
        }
      }
    }
  }
}