.content.order {
  @media screen and (min-width: 769px){
    max-width: 1440px;
    margin: 0 auto;

    .order_button {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 30px 0 70px;
      a.default {
          padding: 15px 40px; 
          font-size: 2.5vmin;
          max-width: 400px;
          margin: 0;
          position: relative;
          margin: 0 10px;
      }
    }
    .order_status.ok {
      width: 80px;
      height: 80px;
      margin: 100px auto;
    }
  }
}