.comments {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
  font-family: "circe";
}

.comments__header {
  background-color: #48709D;
  padding: 10px 20px;
  font-family: "circe";
  font-size: 2.5vmin;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  min-height: 40px;
  color: #fff;
}

.comments__logo-link {
  display: block;
  max-width: 30px;
  max-height: 30px;
  width: 6vmin;
  height: 6vmin;
  text-decoration: none;
  background: url(../img/social/vk_w.png) no-repeat center center;
  background-size: contain;
  text-indent: -9999px;
  
  &:active,
  &:hover {
    text-decoration: none;
  }
}

.comments__top {
  background-color: #fafbfc;
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
  padding: 20px 0;  
}
.comments__user {
  width: 70px;
  height: 70px;
  margin: 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #f0f3f5;
  margin-bottom: 20px;
} 

.comments__user-ico {
  max-width: 100%;
  max-height: auto;

}

.comments__form {
  padding: 0 20px;
  flex: 3 1 auto;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-end;
  
}
.comments__form-textarea {
  width: 100%;
  border: 1px solid #e7e8ec;
  resize: none;
  height: 80px;
  overflow-y: auto;
  background-color: #fff;
  font-size: 2.5vmin;
  padding: 10px;
  margin-bottom: 20px;
  font-weight: 500;
}
.comments__form-submit {
  width: 30%;
}
.comments__form-submit {
  background-color: #5e81a8;
  color: #fff;
  border-radius: 2px;
  padding: 15px 20px;
  font-size: 3vmin;
  border: 0;  
  cursor: pointer;
}
.comments__list {
  background-color: #fff;
  padding: 0;
  margin: 0;
  list-style: none;
}
.comment {
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
  padding: 20px 0;  
}

.comment__user-info {
  padding: 0 20px;
  flex: 3 1 auto;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
}
.comment__user-name {
  color: #48709D;
  font-size: 3vmin;
  font-weight: 500;
  margin-bottom: 5px;
}
.comment__user-text {
  font-size: 3vmin;
  margin-bottom: 10px;
}
.comment__data {
  display: flex;
  width: 100%;
  flex-direction: column;
}
.comment__time {
  color: #B3B2C3;
  margin-bottom: 10px;
  font-size: 2.2vmin;
}
.comment__reply {
  font-size: 1.9vmin;
  margin-bottom: 10px;
  color: #48709D;

  &:active,
  &:hover {
    text-decoration: underline;
  }
}
.comment__reply-form {
  padding: 0;
  width: 100%;
  .comment__reply-input.comment__reply-input {
    padding: 5px 10px !important; 
    border: 1px solid #e7e8ec !important;
    width: 100%;
    font-size: 2.3vmin !important;
    font-weight: 400 !important;
  }
}
.comment__reply-form.hide {
  display: none;
}

.comments__footer {
  background-color: #fff;
  padding: 20px 0;
  display: flex;
  justify-content: center;
}
.comments__more {
  width: 50%;
  border-radius: 2px;
  padding: 15px 20px;
  font-size: 3vmin;
  border: 0;
  cursor: pointer;
  color: #5e81a8;
  background-color: #f0f3f5;
}

