.content.c_cart {

  @media screen and (min-width: 769px){
    max-width: 1440px;
    margin: 0 auto;

    .cart_wrap {
      .item {
        padding: 20px;
        display: flex;
        justify-content: space-between;

        .description {
          display: flex;
          justify-content: space-between;
          .img {
            padding: 10px;
            display: flex;
            align-items: center;
            img {
              max-width: 111px;
              height: auto;
            }
          }
          .main {
            padding: 10px;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: center;
          }
          .name {
            font-size: 2.5vmin;
          }
          button {
            width: 40px;
            height: 40px;
          }
          .choosed {
            font-size: 2vmin;
          }
          
        }
        .info {
          padding: 10px;
          display: flex;
          justify-content: space-between;
          padding-right: 30px;
          align-items: center;
          position: relative;

          .title {
            font-size: 2.5vmin;
            margin: 0;
          }
          .price {
            font-size: 3.5vmin;
          }
          .amount {

            button {
              width: 40px;
              height: 40px;
            }
          }
          .choosed {
            font-size: 2vmin;
            min-width: 20px;
          }
          .delete {
            width: 80px;
            height: 80px;
            position: absolute;
            bottom: -25px;
            right: -25px;
            margin: 0;
          }
        }
      }
    }
    .cart__add-info {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      padding: 0 50px;

      .promo {
        padding: 0;
        margin: 0;
      }
      input, button {
        padding: 10px 20px;
        font-size: 2.5vmin;
      }
      .cart_info {
        margin-top: 20px;
        font-size: 2.5vmin;
        div {
          font-size: 3.5vmin;
        }
      }
      .des.issue {
        padding: 10px 70px;
      }
    }

    .items {
      .add, .added {
        width: 50px;
        height: 50px;
        top: 25px;
        right: 15px;
      }
    }
  }
}