.content.about {
  .contents {
    .main__title {
      display: none;
    }
    @media screen and (min-width: 769px){
      max-width: 1440px;
      margin: 0 auto;
      .main__title {
        display: block;
      }
      .contents__elem {
        .info_block.info_block_tel {
          border-top: 0;
          font-size: 2.5vmin;
          div {
            font-size: 4vmin;
          }
        }
        .info_block {
          padding: 20px;
          font-size: 2.5vmin;
          div {
            font-size: 4vmin;
          }
          .vk_soc_bl, .fb_soc_bl, .inst_soc_bl {
            width: 80px;
            height: 80px;
            margin-top: 0;
          }
        }
        .info_block.location {
          text-align: left;
          div {
            text-align: left;
          }
          .working {
            display: block;
            text-align: left;
          }
        }
        .couple-info_block {
          display: flex;
          justify-content: center;
          .info_block {
            width: 50%;
          }
        }
      }
    }
  }
}