.title.main__title {
  display: block;
  font-size: 5vmin;
  font-weight: 500;
  margin-bottom: 40px;
  padding-left: 40px;

  @include tablet() {
    display: none;
  }
}
.delivery.contents {
  .contents__elem {
    width: 50%;

    &:last-child {
      //padding-top: 65px;
      width: 40%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      margin-left: 100px;
      .contents__elem-title {
        font-size: 3.5vmin;
        font-weight: 500;
        margin-bottom: 30px;

        @include tablet() {
          font-size: 4.48vmin;
        }
      }
    }

  }
  @include tablet() {
    flex-wrap: wrap;
    .contents__elem {
      width: 100%;
      &:last-child {
        width: 100%;
        margin-left: 0;
      }
    }
  }
  .tabs {
    display: flex;
    justify-content: space-between;
    padding-bottom: 12px;

    a {
      font-size: 1.9vmin;
      padding: 0 10px;
      width: 30%;

      &::after {
        height: 7px;
        bottom: -12px;
        left: 11px;
        width: 90%;
        margin: 0 auto;
      }
    }
    a.dis {
      color: #a0a6af;
    }

    @include tablet() {
      display: none;
    }
  }
  .item.item_self {
    font-family: "circe";
    padding: 20px 20px 25px 15px;
    display: flex;
    justify-content: space-between;
    position: relative;
    background-color: #fff;
    border-radius: 10px;
    margin-bottom: 20px;
    .item__data {
      width: 45%;
      padding: 0 5px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: flex-start;
    }
    .name {
        font-weight: 500;
        font-size: 2.5vmin; 
        margin-bottom: 10px;
    }
    .size {
      font-weight: 400;
      font-size: 1.7vmin; 
      color: #9da3ac;
      margin-bottom: 10px;
      span {
        margin-left: 10px;
      }
    }
    .quontity {
      font-weight: 400;
      font-size: 1.7vmin; 
      color: #9da3ac;
      span {
        margin-left: 10px;
      }
    }
    .ico {
      width: 20%;
      max-width: 100px;
      max-height: 230px;
      img {
        max-width: 100%;
        height: auto;
      }
    }
  }
  .price.price_self {
    width: 30%;
    font-family: "circe";
    font-weight: 500;
    padding: 0 5px;
    display: flex;
    justify-content: flex-end;
    .price-val {
      font-size: 2.5vmin;
    }
    .rub {
      font-size: 3vmin;
    }
  }
  .delete.delete_self {
    position: absolute;
    width: 35px;
    height: 35px;
    margin: 0;
    padding: 0;
    bottom: 10px;
    right: 10px;

    .btn {
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: url(../img/m_ico_12.png) no-repeat center center;
      background-size: 38%;
    }
  }
  .end.end_self.t_c {
    font-size: 2vmin;
    a {
      font-size: 3vmin;
    }
    p {
      font-size: 3vmin;
    }
  }
  .windows {
    padding: 30px 10px 10px 10px;
    margin-bottom: 30px;
  }



  @media screen and (min-width: 769px) {
    max-width: 1440px;
    margin: 0 auto;

    .windows {
      .p_title {
        font-size: 3.5vmin;
        padding: 18px 12px;
        text-align: left;
      }
      .contacts_data {
        .l {
          margin-bottom: 20px;
        }
        input {
          padding: 16px 20px;
          font-size: 2vmin;
          border-bottom: 3px solid #000031;
          width: 66%;
        }
        .n_d, .des {
          border: 0;
        }
        .active {
          padding: 15px;
          font-size: 2vmin;
          width: 30%;
        }
        .l.l_rules {
          padding: 20px;
          margin-top: 20px;
          label {
            padding: 0;
            padding-left: 50px;   
            font-size: 2vmin;
            &::before {
              width: 30px;
              height: 30px;
              left: 0;
              top: 30px;
            }
          }
        }
        .des {
          width: 100%;
          padding: 40px;
          font-size: 3vmin;
          margin-top: 50px;
        }
      }
      .delivery {
        .l {
          margin-bottom: 20px;
          .l_title {
            padding: 10px;
            font-size: 3.5vmin;
          }
          .delivery_type_line label {
            min-width: 150px;
            width: 20vmin;
           
          }
          .wrap {
            label {
              padding: 5px;
            }
            .title, .price, .info {
              font-size: 1.9vmin;
            }
            .title {
              padding-left: 45px;
              &::before {
                width: 40px;
                height: 40px;
                top: 0;
                margin-top: 0;
                padding: 0;
              }
            }
            .price {
              margin-bottom: 0;
              width: 100%;
              display: flex;
              justify-content: flex-start; 
            }
          }
          input  {
            padding: 10px;
            font-size: 2vmin;
            border-bottom: 3px solid #000031;

          }
          input.user__data {
            width: 30%;
            margin-left: 20px;

            &:first-child {
              margin-left: 0;
            }
          }
        }
        .promo {
          padding: 0; 
          margin: 40px 0;
          .active, .n_d {
            font-size: 2vmin;
            min-height: 60px;
            padding: 0;

            
          }
          .n_d {
            padding-left: 10px;
          }
        }
        .buttons {
          label {
            .title {
              margin-bottom: 10px;
            }
          }
        }
        .text {
          font-size: 2.5vmin;
          padding: 10px;
          line-height: 1.2;
          margin-bottom: 20px;
        }
        .buttons.b_pay label {
          padding: 35px 15px 35px 45px;
          font-size: 2.5vmin;
          position: relative;
          line-height: 1.1;
          text-align: center;

          &:before {
            width: 35px;
            height: 35px;
            top: 8vmin;
            left: 5px;
          }
        }
        .i_title {
          padding: 10px;
          font-size: 2vmin;
        }
        .smart_input {
          .flags {
            top: 0;

            .chosen {
              padding: 20px;
            }
          }
          input {
            padding: 20px;
            border-bottom: 3px solid #000031;
            font-size: 2.5vmin;
          }
        }
        .result {
          margin-bottom: 45px;
          font-size: 4vmin;
          div {
            font-size: 5vmin;
            margin: 20px 0;
          }
        }
        .des {
          padding: 15px 20px;
          font-size: 2.5vmin;
          min-height: 100px;
        }
        .l_rules  {
          padding: 20px;
          margin-top: 20px;
          label {
            padding: 0;
            padding-left: 50px;   
            font-size: 2.5vmin;
            &::before {
              width: 30px;
              height: 30px;
              left: 0;
              top: 3vmin;
            }
          }
        }
      }
    }
  }
}

.delivery.contents {
  .contents__elem {

    @media screen and (min-width: 769px) {
      .cart_wrap {
        padding: 5px;

        .item {
          border-radius: 4px;
          padding: 4px;
          margin-bottom: 4px;
          box-shadow: 0 1px 4px -1px rgba(52,70,74,0.29);
          display: block;
          text-align: center;
        }

        .description {
          .img {
            padding: 6px;
            width: 30%;

            img {
              max-width: 71px; 
            }
          }

          .main {
            padding: 6px;
          }
          .name {
            font-size: 14px;
          }
          .size {
            margin-top: 6px;
            .choosed {
              font-size: 10px;
              padding: 0 8px;
            }
          } 
        }
        button {
          width: 16px;
          height: 16px;
          border: 1px solid #e9eef3;
          font-size: 10px;
        }
        .info {
          padding-left: 10px;
          padding-bottom: 2px;
          .title {
            font-size: 10px;
            margin-bottom: 0;
            margin-top: 2px;
            text-align: center;
          }
          .price {
            font-size: 14px;
            padding-top: 4px;
            display: block !important;
            width: 100% !important;
          }
          .amount {
            padding-top: 0;
          }
          .amount[dv-amount] div {
            min-width: 28px;
            font-size: 14px;
            padding: 4px 4px;
            border-radius: 4px;
            padding-bottom: 2px;
            margin: 0 6px;
            border: 1px solid #e9eef3;
          }
          .c:last-child  {
            width: 56px;
          }
          .delete {
            width: 47px;
            height: 46px;
            margin-left: 8px;
          }
        }
      }
    }
  }
}
