.content.good.view {
  
  .slider__list {
    display: none;
  }
  .buy.des {
    display: block;
  }
  .reviews {
    .text {
      font-size: 2vmin;
      line-height: 1.3;
      color: #74748f;
      width: 65%;
      margin: 20px auto;
      text-align: center;
    }
  }
  .table_sizes {
    display: none;
  }
  .contents .ldl .like {
    &:hover {

    }
  }
  @media screen and (min-width: 769px){
    .contents {
        max-width: 1440px;
        margin: 0 auto;
        justify-content: space-between;

        .contents__elem {
          width: 60%;
        }
        .contents__elem:last-child {
          width: 37%;
        }

        .main {
          border-top: 0;
        }
        .view_good {
          padding: 40px;
          min-height: 927px;
          box-shadow: 0 4px 10px rgba(52,70,74,.29);
          border-radius: .7475vmin;

          img {
            height: 100%;
            width: 100%;
          }
          .back {
            width: 80px;
            height: 80px;
            bottom: 50px;
            left: 10px;
          }
        }
        .good_types {
          .types_line {
            padding: 20px 0;
            box-shadow: 0 4px 10px rgba(52,70,74,.29);
            border-radius: .7475vmin;
          }
          .types {
            display: flex;
            justify-content: space-between;
          }
          .type {
            img {
              max-height: 200px;
              width: auto;
            }
            .title {
              font-size: 2vmin;
            }
          }
          .tabs {
            display: none;
            a {
              font-size: 1.9vmin;
              padding: 20px;
            }
          }
          .title {
            font-size: 3vmin;
          }
        }
        .good_sizes {
          padding: 20px 0;
          
          .sizes {
            border-spacing: 0;
            display: flex;
            min-height: 150px;
            justify-content: center;
            align-items: center;

            .chooser {
              top: 22px;
              bottom: 28px;
            }
          }

          .b_title, .simple_text {
            font-size: 3vmin;
          }
          .sizes_line {
            margin: 10px 0;
          }
          .size {
            padding: 15px;
            font-size: 2.4vmin;
          }
        }
        .good_price {
          margin: 30px 0;

          .price-val {
            font-size: 5vmin;
          }
          .rub {
            font-size: 5vmin;
          }
        }
        .info {
          margin: 20px 0;
          padding: 0;
          .name {
            font-size: 4vmin;
          }
          .g_tags {
            margin: 5px 0;
          }
          .g_tags li {
            font-size: 2vmin;
          }
          .artist {
            font-size: 2vmin;
          }
        }
        .ldl {
          width: 25%;
          padding: 20px 0;
          .like, .dislike {
            width: 50px;
            height: 50px;
            background-size: cover;
          }
          div {
            font-size: 1.9vmin;
            margin: 20px 0;
          }
        }
        .buy {
          display: block;
          padding: 20px 40px;
          font-size: 3.5vmin;
        }
        .tabs {
          a {
            font-size: 2vmin;
            padding: 20px 0;
          }
        }
        .g_info {
          .window  {
            font-size: 2vmin;
            padding: 20px;
          }
        }
      }

    .slider {
      display: none;
    }
    .slider__list {
      display: flex;
      flex-wrap:wrap;

      .slide {
        width: 31%;
        margin: 10px;
        min-width: 340px;

        @media screen and (max-width: 1080px) {
          width: 100%;
        }
        .img {
          height: 210px;  
        }
        .title {
          font-size: 2.5vmin;
          padding-top: 20px;
          padding-bottom: 10px;
        }
        .text {
          font-size: 2vmin;
          line-height: 1.3;
        }
      }
    }  
    .s_title {
      font-size: 3.5vmin;
    }
    .reviews {
      center {
        max-width: 600px;
        margin: 0 auto;
      }
      .text {
        font-size: 2vmin;
        line-height: 1.3;
        color: #74748f;
        width: 65%;
        margin: 20px auto;
        text-align: center;
      }
    }
    .table_sizes {
      display: block;
      padding: 40px 40px 0 40px;
      .title {
        font-size: 3vmin;
        padding: 40px;
        line-height: 1.5;

        &:after {
          position: absolute;
          right: -8px;
          top: 20vmin;
          width: 100px;
          height: 150px;
          background: url(../img/m_t-shirt.png) no-repeat center center;
          background-size: contain;
        }
      }
      .own_size_info {
        padding-left: 6.8520833333333vmin;
        position: relative;
        font-size: 2.99vmin;
        font-weight: 500;
        color: #8b93a0;
        margin: 4.3604166666667vmin 0;
      }
      .table {
        display: table;
        width: 100%;
      }
      .table_line {
        display: table-row;
      }
      .table_item {
        display: table-cell;
        text-align: center;
        width: 12.5%;
        font-size: 3vmin;
        font-weight: 700;
        padding: 2.2425vmin 0.62291666666667vmin;
        color: #132136;
      }
      .table_line:nth-child(1) .table_item {
        font-weight: 500;
        color: #fe2e71;
      }
      .table_item:not(:first-child) {
        border-left: 0.37375vmin solid #d6dce2;
      }
      .table_line:nth-child(2) .table_item {
        font-weight: 500;
        color: #6ad3ff;
      }
      .table_item:not(:first-child) {
        border-left: 0.37375vmin solid #d6dce2;
      }
    }
    .advantages {
      .s_title {
        display: none;
      }
    }
   
  }

}

.popup.is.theme_size {
  @media screen and (min-width: 769px){ 
    //display: none;
  }
}