.content.searching {
  .title.main__title {
    display: none;
  }
  .search.searching__form {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 0;
    height: auto;
    
  }
  .s_title.t_l {
    margin-top: 30px;
  }
   @media screen and (min-width: 769px){
     max-width: 1440px;
     margin: 0 auto;
     padding: 20px;

     .title.main__title {
       display: block;
       font-size: 4vmin;
       font-family: "circe";
       font-weight: 500;
     }

     .search.searching__form {
       display: block;
       min-width: 100%;
       padding: 0;
       margin-top: 30px;
       margin-bottom: 30px;
        input {
          padding: 10px 20px;
          width: 100%;
          font-size: 3vmin;
        }
        .searching__btn {
          width: 40px;
          height: 40px;
          top: 50%;
          right: 0;
          transform: translateY(-20px);
        }
     }
     .s_title.t_l, .s_title.in_search {
       font-size: 3.5vmin;
     }
     .sm_title.t_c.in_search {
       font-size: 3vmin;
       margin-top: 30px;
     }
     .tags.clear {
       width: 100% !important;
       li {
         margin: 0 10px;

         a {
           padding: 15px 30px;
           font-size: 3vmin;
         }
       }
     }
   } 
}