.menu,
.innermenu {
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.menu__elem {
  margin-left: 40px;
  position: relative;

  &:first-child {
    margin-left: 0;
  }
  @media screen and (max-width: 960px) {
    margin-left: 15px;
  }
}

.menu__elem_sales {
  padding-right: 30px;
  border-right: 2px solid #e7ebef;
  position: relative;

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: -15px;
    left: -25px;
    width: 21px;
    height: 21px;

    @include sprite($s-percent);
  }
}

.menu__elem_more {
  padding-left: 40px;
  border-left: 2px solid #e7ebef;
  text-align: left;

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: -15px;
    left: 10px;
    width: 21px;
    height: 21px;

    @include sprite($s-heart);
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0px;
    right: -25px;
    width: 21px;
    height: 21px;

    @include sprite($s-arrowd);
  }
}

.menu__elem_category {
  margin-left: 50px;
  text-align: left; 

  svg {
    display: block;
    position: absolute;
    top: -15px;
    left: -24px;
    width: 21px;
    height: 21px;
    fill: #d4d5dc;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0px;
    right: -25px;
    width: 21px;
    height: 21px;

    @include sprite($s-arrowd);
  }
}

.menu__link {
  display: flex !important;
  justify-content: space-between;
  align-items: center;
  color: #000031;
  font-family: circe;
  font-weight: 400;
  font-size: 16px !important;
  text-decoration: none;
  margin-bottom: 8px;
  padding: 0 !important;

  &:active,
  &:hover {

  }

  @media screen and (max-width: 1240px) {
    display: block !important;
  }
}
.menu__link_submenu {
  display: block !important;
  float: none !important;
}
.menu__elem-title {
  font-family: circe;
  font-weight: 500;
  font-size: 18px;
  margin-bottom: 5px;
}
.menu__elem-val {
  font-family: circe;
  font-weight: 400;
  font-size: 15px;
}

.menu__elem-svg {
  display: inline-block;

  svg {
    height: 11px;
    width: 12px;
    fill: #132136;
  }

}

.menu__img {
  height: 75px;
  width: 75px;
  border-radius: 50%;
  background-color: #f0f3f5;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  opacity: 0.6;
  transition: opacity 50ms ease-out;
  transition: background-image 50ms ease-out;


  @media screen and (max-width: 1240px) {
    margin-bottom: 5px;
  }

  // &::before {
  //   position: absolute;
  //   content: '';
  //   display: block;
  //   width: 37px;
  //   height: 4px;
  //   top: 15px;
  //   left: 19px;

  //   @include sprite($s-shirt);
  // }
  .menu__img-item {
    width: 100%;
    height: 100%;
    opacity: 0.6;
    transition: opacity 50ms ease-out;

    &:hover {
      opacity: 1;
    }
  }

}

.menu__img.menu__img-tshirt.tshirt-m {
  background-image: url('../img/tshirt-m.png');
}
.menu__img.menu__img-tshirt.tshirt-f {
  background-image: url('../img/tshirt-f.png');
}

.menu__img.menu__img-shirt.shirt-m {
  background-image: url('../img/shirt-m.png');
}
.menu__img.menu__img-shirt.shirt-f {
  background-image: url('../img/shirt-f.png');
}

.menu__img.menu__img-longsleeve.longsleeve-m {
  background-image: url('../img/longsleeve-m.png');
}
.menu__img.menu__img-longsleeve.longsleeve-f {
  background-image: url('../img/longsleeve-f.png');
}

.menu__img.menu__img-reglan.reglan-m {
  background-image: url('../img/reglan-m.png');
}
.menu__img.menu__img-reglan.reglan-f {
  background-image: url('../img/reglan-f.png');
}

.menu__link:hover > .menu__img {
  opacity: 1;
}

.menu__link:active > .tshirt-f,
.menu__link:focus > .tshirt-f {
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.21), rgba(0, 255, 255, 0.21)),
    url('../img/tshirt-f.png');

}

.menu__link:active > .tshirt-m,
.menu__link:focus > .tshirt-m {
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.21), rgba(0, 255, 255, 0.21)),
    url('../img/tshirt-m.png');

}

.menu__link:active > .shirt-m,
.menu__link:focus > .shirt-m {
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.21), rgba(0, 255, 255, 0.21)),
    url('../img/shirt-m.png');

}

.menu__link:active > .shirt-f,
.menu__link:focus > .shirt-f {
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.21), rgba(0, 255, 255, 0.21)),
    url('../img/shirt-f.png');

}

.menu__link:active > .longsleeve-m,
.menu__link:focus > .longsleeve-m {
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.21), rgba(0, 255, 255, 0.21)),
    url('../img/longsleeve-m.png');

}

.menu__link:active > .longsleeve-f,
.menu__link:focus > .longsleeve-f {
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.21), rgba(0, 255, 255, 0.21)),
    url('../img/longsleeve-f.png');

}

.menu__link:active > .reglan-m,
.menu__link:focus > .reglan-m {
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.21), rgba(0, 255, 255, 0.21)),
    url('../img/reglan-m.png');

}

.menu__link:active > .reglan-f,
.menu__link:focus > .reglan-f {
  background-image: 
    linear-gradient(rgba(0, 255, 255, 0.21), rgba(0, 255, 255, 0.21)),
    url('../img/reglan-f.png');

}



.menu__data {
  margin-left: 10px;
}

.menu__sales,
.menu__title {
  color: #afb8c7;
  font-size: 25px;
}

.innermenu {
  width: 100px;
  background-color: #fff;
  box-shadow: 0px 1px 4px 1px rgba(0, 0, 0, 0.05);
  padding: 5px;
  font-size: 14px;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 99;
}

.menu__title {
  cursor: pointer;
  position: relative;
  font-size: 16px;

  &:hover > .innermenu {
    display: block;
  }
}

.innermenu__item {
  margin-bottom: 10px;
  padding-left: 10px;

  .innermenu__link {
    display: block;
    color: #000031;
    font-family: circe;
    text-decoration: none;
    padding: 5px;
    font-size: 12px;

    &:active,
    &:hover {
      color: rgba(0, 0, 49, 0.43);
    }
  }
}

